/**
 * SEO component that queries for data with
 * Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/how-to/querying-data/use-static-query/
 */
import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { Helmet } from "react-helmet"
import { useLocation } from "@reach/router"

function Seo({ description, title, children, image }) {
  const location = useLocation()
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
            keywords
            siteUrl
            image
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description
  const defaultTitle = site.siteMetadata?.title
  const keywords = site.siteMetadata?.keywords || []
  const metaImage = image || site.siteMetadata?.image
  const imageUrl = `${site.siteMetadata?.siteUrl}${metaImage}`
  const siteUrl = site.siteMetadata?.siteUrl
  const canonicalUrl = `${site.siteMetadata?.siteUrl}${location.pathname}`

  return (
    <>
      <Helmet>
        <title>{defaultTitle ? `${title} | ${defaultTitle}` : title}</title>

        {/* Essential SEO Meta Tags */}
        <meta name="description" content={metaDescription} />
        <meta name="keywords" content={keywords.join(", ")} />
        <link
          rel="canonical"
          href={canonicalUrl || "https://appnetwise.com/"}
        />

        {/* Open Graph Meta Tags (Facebook, LinkedIn, etc.) */}
        <meta
          property="og:title"
          content={title || "Digital Transformation Partner"}
        />
        <meta property="og:description" content={metaDescription} />
        <meta property="og:type" content="website" />
        <meta property="og:image" content={imageUrl} />
        <meta property="og:url" content={siteUrl} />

        {/* Twitter Card Meta Tags */}
        <meta name="twitter:card" content="summary" />
        <meta
          name="twitter:creator"
          content={site.siteMetadata?.author || ""}
        />
        <meta
          name="twitter:title"
          content={title || "Digital Transformation Partner"}
        />
        <meta name="twitter:description" content={metaDescription} />
        <meta name="twitter:image" content={imageUrl} />

        {/* Google Search Console Verification */}
        <meta
          name="google-site-verification"
          content="KjsL8qPsjdT_ZvywznPIEplC8GSK7-VmJj_VOqaijc8"
        />

        {children}
      </Helmet>
    </>
  )
}

export default Seo
